



















































































































































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { Filters, PartidoEnum } from "../models/Event";
import EventBus from "../utils/eventbus";

@Component
export default class Header extends Vue {
  @Prop() text!: string;
  expand = false;
  dateFrom: string | null = null;
  dateFromFormatted: string | null = null;
  dateTo: string | null = null;
  dateToFormatted: string | null = null;
  menuFrom = false;
  menuTo = false;
  filterApplied = false;
  textFilter = "";
  partidos = [];

  formatDate(date: string | null) {
    if (!date) return null;

    const [year, month, day] = date.split("-");
    return `${day}/${month}/${year}`;
  }

  @Watch("dateFrom")
  formatDateFrom() {
    this.dateFromFormatted = this.formatDate(this.dateFrom);
  }

  @Watch("dateTo")
  formatDateTo() {
    this.dateToFormatted = this.formatDate(this.dateTo);
  }

  parseDate(date: string) {
    if (!date) return null;

    const [month, day, year] = date.split("/");
    return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
  }

  applyFilter() {
    this.filterApplied = true;
    this.expand = false;
    const partidos: PartidoEnum[] = [];
    for (let i = 0; i < this.partidos.length; i++) {
      switch (this.partidos[i]) {
        case 0:
          partidos.push("PN");
          break;
        case 1:
          partidos.push("PC");
          break;
        case 2:
          partidos.push("CA");
          break;
        case 3:
          partidos.push("PI");
          break;
        case 4:
          partidos.push("PG");
          break;
      }
    }

    const filters: Filters = {
      dateFrom: this.dateFromFormatted,
      dateTo: this.dateToFormatted,
      filterText: this.textFilter,
      partidos: partidos
    };
    EventBus.$emit("APPLY_FILTER", filters);
  }

  save() {
    return true;
  }

  cancelFilter() {
    this.partidos = [];
    this.dateFrom = null;
    this.dateTo = null;
    this.filterApplied = false;
    this.textFilter = "";
    EventBus.$emit("UNDO_FILTER");
  }

  goToTwitter() {
    window.location.href = "https://twitter.com/coloridospillos";
  }

  goToHome() {
    this.expand = false;
    this.$router.push("/");
  }

  goToPilleada() {
    this.expand = false;
    this.$router.push("/pilleada");
  }

  cancelMenu1() {
    this.menuFrom = false;
    this.dateFrom = null;
  }

  cancelMenu2() {
    this.menuTo = false;
    this.dateTo = null;
  }

  updated() {
    const element: any = this.$refs["tags-group"];
    element.onResize();
  }
}
